.login {
  display: grid;
  place-items: center;
  height: 100vh;
  background-color: black;
}

.login > img {
  width: 100%;
}

.login > a {
  padding: 20px;
  border-radius: 99px;
  font-weight: 800;
  color: white;
  background-color: #1db954;
  /* text-decoration- to not get an underline for anchor link when hovered */
  text-decoration: none;
}
