.songRow {
  display: flex;
  margin-left: 20px;
  align-items: center;
  color: white;
  padding: 20px;
  z-index: 100;
}
.songRow:hover {
  cursor: pointer;
  background-color: black;
  opacity: 0.8;
}
.songRow__info {
  margin-left: 20px;
}
.songRow__info > h1 {
  font-size: 16px;
}
.songRow__info > p {
  font-size: 14px;
  margin-top: 3px;
  color: gray;
}
.songRow__album {
  height: 40px;
  width: 40px;
}
